.fp-section {
  text-align: center;
}

button {
  padding: 0.93em 1.87em;
  background: #35495e;
  border-radius: 5px;
  border-color: transparent;
  display: block;
  color: #fff;
  margin: 0 auto;
  cursor: pointer;
  font-size: 0.85em;
}

.home {
  height: 100vh;
  width: auto;
  color: #aaa;
  background-image: url('./assets/gray-abstract-wireframe-technology-background.jpg');
  background-size: cover;
  display: flex;
  justify-content: center;
}

.home-main-container {
  height: 100%;
  width: 100%;
  display: flex;
  justify-content: center;
}

.home-top-bar {
  position: absolute;
  top: 0;
  width: 75%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.logo {
  height: 5rem;
  width: auto;
}

.top-bar-links {
  margin-right: 2rem;
  display: flex;
  justify-content: space-between;
  width: 10%;
}

.home h3 {
  color: #aaa;
}

.home h4 {
  font-size: 1.5rem;
}

.profile-img {
  object-fit: cover;
  max-height: 70rem;
  max-width: 100%;
  width: auto;
  opacity: 95%;
  border-radius: 5px;
}

.profile-img-container {
  border-radius: 100%;
  height: 40rem;
  width: 40rem;
  overflow: hidden;
}

.subtitle-container {
  display: flex;
  justify-content: center;
  gap: 0.5rem;
  align-items: center;
  height: 1.5rem;
  /* width: 50%; */
}

.subtitle-icon {
  height: 8px;
  width: 8px;
  border-radius: 100%;
  background-color: #aaa;
}

.title-card {
  position: absolute;
  top: 10%;
  display: flex;
  flex-flow: column;
  justify-content: start;
  gap: 1rem;
  height: 3rem;
}

.main-title-container {
  display: flex;
  gap: 0.5rem;
  margin-bottom: 0;
}

body {
  background: #0b1d2a;
}

.arrows {
  width: 60px;
  height: 72px;
  position: absolute;
  left: 50%;
  margin-left: -30px;
  bottom: 20px;
  cursor: pointer;
}

.arrows path {
  stroke: #aaa;
  fill: transparent;
  stroke-width: 4px;
  animation: arrow 2s infinite;
  -webkit-animation: arrow 2s infinite;
}

@keyframes arrow {
  0% {
    opacity: 0;
  }
  40% {
    opacity: 1;
  }
  80% {
    opacity: 0;
  }
  100% {
    opacity: 0;
  }
}

@-webkit-keyframes arrow /*Safari and Chrome*/ {
  0% {
    opacity: 0;
  }
  40% {
    opacity: 1;
  }
  80% {
    opacity: 0;
  }
  100% {
    opacity: 0;
  }
}

.arrows path.a1 {
  animation-delay: -1s;
  -webkit-animation-delay: -1s; /* Safari 和 Chrome */
}

.arrows path.a2 {
  animation-delay: -0.5s;
  -webkit-animation-delay: -0.5s; /* Safari 和 Chrome */
}

.arrows path.a3 {
  animation-delay: 0s;
  -webkit-animation-delay: 0s; /* Safari 和 Chrome */
}

.projects-container {
  height: 100vh;
  width: 100%;
  display: flex;
  flex-flow: column;
  justify-content: center;
  cursor: pointer;
}

.project-header {
  font-size: 0.75rem;
}

.projects {
  width: auto;
  height: 75vh;
  display: grid;
  grid-template-columns: repeat(3, minmax(0, 1fr));
  /* flex-flow: row wrap;
  justify-content: center;
  align-items: flex-start; */
  padding: 2rem;
  gap: 5rem;
  flex-shrink: 1;
}

.portfolio-image {
  max-height: 10rem;
  width: 100%;
  flex-shrink: 1;
}

.project-container {
  grid-column: span 1 / span 1;
  display: flex;
  flex-flow: column;
  flex-shrink: 1;
}

.project-link {
  text-decoration: none;
  color: black;
  font-family: inherit;
}

.icon {
  height: 2rem;
  width: auto;
}

.icon:hover {
  box-shadow: 1px 1px 10px 2px gray;
  border-radius: 5px;
}

.about-me-container {
  height: 100vh;
  width: 100%;
  display: flex;
  flex-flow: column;
  align-items: center;
  justify-content: space-between;
  color: #35495e;
}

.about-me-header {
  font-size: 1.5rem;
  color: #263238;
}

.about-me-sub {
  height: 100%;
  width: 100%;
  padding-bottom: 10rem;
  display: flex;
  flex-flow: column;
  align-items: center;
  justify-content: space-evenly;
}

.content-container {
  width: 90%;
  display: flex;
  justify-content: space-around;
  padding: 0 2rem;
}

.about-main-body {
  width: 25%;
  display: flex;
  flex-flow: column;
  align-items: flex-start;
}

.about-main-body p {
  text-align: start;
}

.about-main-body ul {
  display: flex;
  flex-flow: column;
  justify-content: flex-start;
  align-items: flex-start;
}
